.snippet-container {
    position: relative;
    width: 25vw;
    display: flex;
    border: 1px solid #535d63;
    margin: 3vh;
}

.snippet-image {
    width: 100%;
    height: 100%;
    border-right: 1px solid #535d63;
}

.snippet-header {
    color: #535d63;
    margin-bottom: 1.5vh;
}

.onHoverTitle {
    color: #535d63;
}


.scale {
    transition: transform .1s ease-in;
}

.scale:hover {
    transform: scale(1.025);
}

.scalingImage-container h2 {
    margin-bottom: 2vh;
}

@media only screen and (min-width: 651px) {
    .scalingImage, .scale {
        width: 25vw;
    }

    .onHover {
        opacity: 0;
        position: absolute;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        height: 100%;
    }
    
    .onHover:hover {
        color: white;
        opacity: .8;
        transition: opacity .1s ease-in;
    
    }
}

@media only screen and (max-width: 650px) {
    .snippet-container {
        position: relative;
        width: 40vw;
        display: flex;
        border: 1px solid #535d63;
        margin: 3vh;
        justify-content: center;
        align-items: center;
    }

    .onHover {
        opacity: .75;
        position: absolute;
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
        height: 100%;
        height: auto;
        text-align: center;
        border: 1px solid #535d63; 
    }

}


@media only screen and (max-width: 450px) {

    .snippet-container {
        width: 80vw;
    }
}
