.head {
    display: flex;
    justify-content: space-between;
    margin-bottom: 6vh;
}


#logo {
    z-index: 2;
    width: 15vw;
    margin: 4vh 0 0 2vw;

}

#logo:hover {
    transform: scale(1.1);
    transition: transform .1s ease-in; 
}

#logo-pic {
    width: 15vw;
}

@media only screen and (max-width: 650px) {
    .head {
        margin-bottom: 9vh;
    }
}