.navBar {
    position: absolute;
    display: flex;
    justify-content: space-evenly;;
    width: 40vw;
    margin-top: 2vh;
    z-index: 2;
    align-self: flex-end;
    top: 15vh;
    right: 3vw;
}


.hamburger-container {
    position: fixed;
    top: 1vh;
    right: 1vw;
    cursor: pointer;
    width: 20vw;
    z-index: 200;
  }
  
  .bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    margin: 6px 0;
    transition: 0.4s;
  }

  .color-standard {
    background-color: #535d63; 
  }

  .color-white {
      background-color: white;
  }
  
  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }
  
  .change .bar2 {opacity: 0;}
  
  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }


  .drop-down-container {
      background: #535d63;
      width: 60vw;
      height: 100vh;
      z-index: 100;
      position: fixed;
      top: 0;
      right: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
  }

  .vertical-link {
      color: white;
      padding: .6em;
      font-size: 1.3em;
  }
