.about-me {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.about-me img {
    height: 30vh;
    margin: 2vh 4vw;
}

.about-me p { 
    width: 70vw;
    margin: 6vh 0;
    text-indent: 50px;
}

.column-image {
    display: flex;
    flex-direction: column;
}

.dot {
    height: 10px;
    width: 10px;
    background-color: #535d63;
    border-radius: 50%;
    display: inline-block;
    margin: 0 2vw;
  }

.about-me h1 {
    text-align: center;
}

.aboutme-container .title {
    margin: 8vh 0 -4vh 0;
}

.about-me .submit-btn {
    margin-right: 4vw;
}


.about-me h3 {
    color: #535d63;
    display: block;
    width: 128px;
}

.word-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 70vw;
    margin-bottom: 6vh;
}

.title {
    color: #535d63;
}

@media only screen and (max-width: 660px) {
    .about-me {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .aboutme-container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    #aboutMe2, #aboutMe3 {
        display: none;
    }

    #aboutMe1 {
        height: auto;
        width: 80vw;
    }

    .word-container {
        display: none;
    }

    .aboutme-container .title {
        margin: 6vh 0 0 0;
    }


}
